function getBlockSettings( settings, blockName ) {
    if ( 'core/image' !== blockName ) {
		return settings;
	}
	const { merge } = lodash;

	const imageAttr = [
		'src',
		'alt',
		'data-pin-description',
		'data-pin-id',
		'data-pin-nopin'
	];

	const pintAttr = {
		pinterestDesc: {
			type: 'string',
			source: 'attribute',
			selector: 'img',
			attribute: 'data-pin-description',
			default: '',
		},
		pinterestRepinId: {
			type: 'string',
			source: 'attribute',
			selector: 'img',
			attribute: 'data-pin-id',
			default: '',
		},
		pinterestNoPin: {
			type: 'boolean',
			source: 'attribute',
			selector: 'img',
			attribute: 'data-pin-nopin',
			default: '',
		}
	};

	// Register data pinterest attributes in Image Block.
	settings.attributes = Object.assign( settings.attributes, pintAttr );

	// raw <img> HTML transformation.
	settings.transforms.from[0] = merge( settings.transforms.from[0], {
		schema: {
			figure: {
				children: {
					a: {
						children: {
							img: {
								attributes: imageAttr
							}
						}
					},
					img: {
						attributes: imageAttr
					}
				}
			}
		}
	} );

	// [caption] shortcode transformation
	settings.transforms.from[2].attributes = Object.assign( settings.transforms.from[2].attributes, pintAttr );

	return settings;
}

wp.hooks.addFilter(
    'blocks.registerBlockType',
    'codextune/pin-master',
    getBlockSettings
);

