/**
* Save the value
*
* @return void 
*/

const {
	isEmpty
} = lodash;

const {
	RawHTML,
	renderToString
} = wp.element;

const { hooks } = wp;

const SavePint = ( element, blockType, attributes ) => {
	if ( 'core/image' !== blockType.name ) {
		return element;
	}

	const {
		pinterestDesc,
		pinterestRepinId,
		pinterestNoPin
	} = attributes;

	const imgProps = [];

	if ( ! isEmpty( pinterestDesc ) ) {
		imgProps.push({
			attribute: 'data-pin-description',
			className: '',
			value: pinterestDesc
		});
	}

	if ( ! isEmpty( pinterestRepinId ) ) {
		imgProps.push({
			attribute: 'data-pin-id',
			className: '',
			value: pinterestRepinId
		});
	}

	if ( pinterestNoPin ) {
		imgProps.push({
			attribute: 'data-pin-nopin',
			className: 'nopin',
			value: 'true'
		});

	}

	// If empty, no need to modify.
	if ( isEmpty( imgProps ) ) {
		return element;
	}

	let elementAsString = renderToString( element );

	imgProps.forEach(({ attribute, value, className }) => {

		// Allow limited html to prevent breaking out.
		value = value
		.replace( /&/g, '&amp;' )
		.replace( /</g, '' )
		.replace( />/g, '' )
		.replace( /\"/g, '' );
		elementAsString = elementAsString
		.replace( '<img ', `<img ${attribute}="${value}" ` )
		.replace( ' class="wp-image-', ` class="${className} wp-image-` );
	});

	return (
		<RawHTML>{elementAsString}</RawHTML>
		);
};

wp.hooks.addFilter( 
	'blocks.getSaveElement', 
	'codextune/pin-master', 
	SavePint 
);

