/**
 * Internal block libraries
 */
const { __ } = wp.i18n;

const {
	InspectorControls
} = wp.editor;

const {
	Fragment
} = wp.element;

const {
	CheckboxControl,
	PanelBody,
	TextControl,
	TextareaControl,
} = wp.components;

const {
	createHigherOrderComponent
} = wp.compose;


const {
	hooks,
	media
} = wp;

const pastIdProps = {};

const registerFields = createHigherOrderComponent( ( BlockEdit ) => {
	return ( props ) => {
		if ( 'core/image' !== props.name ) {
			return (
				<BlockEdit {...props} />
			);
		}

		const {
			attributes,
			setAttributes
		} = props;

		// When the last known 'id' prop was known and not an image ID,
		// see if there's some Pinterest Text
		// or Repin value to take from the Media Library.
		if ( typeof pastIdProps[ props.clientId ] !== 'undefined'
			&& ! pastIdProps[ props.clientId ]
			&& attributes.id ) {
			const attachment = wp.media.attachment( attributes.id );
			setAttributes({
				pinterestDesc: attachment.attributes.pm_pinterest_desc,
				pinterestRepinId: attachment.attributes.pm_pinterest_repin_id
			});
		}

		// Track last props state for the next cyle.
		pastIdProps[ props.clientId ] = attributes.id || false;

		if ( ! attributes.id ) {
			return (
				<BlockEdit {...props} />
			);
		}

		const {
			pinterestDesc,
			pinterestRepinId,
			pinterestNoPin
		} = attributes;

		return (
			<Fragment>
				<InspectorControls>
					<PanelBody title={ __('WP Pin Master', 'pin-master') }>
						<TextareaControl
							defaultValue={ pinterestDesc }
							onChange={( value ) => setAttributes({ pinterestDesc: value })}
							label={ __('Pinterest - Description', 'pin-master') }
							placeholder={ '' }
							/>
						<TextControl
							defaultValue={ pinterestRepinId }
							onChange={( value ) => setAttributes({ pinterestRepinId: value })}
							label={ __('Pinterest - Repin ID', 'pin-master') }
							/>
						<CheckboxControl
							checked={ pinterestNoPin }
							onChange={( value ) => setAttributes({ pinterestNoPin: value })}
							label={ __('No Pin - Disable Pinning', 'pin-master') }
							/>
					</PanelBody>
				</InspectorControls>
				<BlockEdit { ...props } />
			</Fragment>
		);
	};
} );

wp.hooks.addFilter( 
'editor.BlockEdit', 
'codextune/pin-master', 
registerFields 
);
